@font-face{
	font-family: 'MarvelFont';
	font-style: normal;

	src: 
	local('MarvelFont'),
	url(../src/font/BentonSansExtraCompBlackRegular.otf) format('openType');
}


body{
	font-family: 'MarvelFont', sans-serif;
	padding-top: 80px;
	background: -webkit-gradient(linear, left top, right top, from(#d5220f), color-stop(#ecf0f1), to(#d5220f));
	background: -webkit-linear-gradient(left, #d5220f, #ecf0f1, #d5220f);
	background: -o-linear-gradient(left, #d5220f, #ecf0f1, #d5220f);
	background: linear-gradient(to right, #d5220f, #ecf0f1, #d5220f);

}



